<template>
  <div>
    <mrc_ess_iconGroup :is_disabled='$store.state.iconstatus'
                       :showReturn='false'
                       @handleEdit='handleEdit'
                       @handleDel='handleDel'></mrc_ess_iconGroup>

    <v-tabs v-model="tab"
            grow
            @change="tabchange"
            background-color="#eeeef0"
            color='primary'
            dark>
      <v-tab v-for="item in items"
             style="color:black"
             :key="item.tab">
        {{ item.tab }}
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item>
        <editGeneral :readonly='readonly'></editGeneral>
      </v-tab-item>

      <v-tab-item>
        <staffGroup :readonly='readonly'></staffGroup>
      </v-tab-item>

    </v-tabs-items>
  </div>
</template>

<script>
import editGeneral from '../modules/editGeneral'
import staffGroup from '../modules/staffGroup'
export default {
  components: {
    editGeneral,
    staffGroup
  },
  props: {},
  data () {
    return {
      tab: 0,
      items: [
        { tab: 'General' },
        { tab: 'Staff Property Group' },
      ],
      e1: 1,
      readonly: true,//默认只读
    };
  },
  beforeDestroy () {
    this.$store.commit('setTab', '')
  },
  created () {
  },
  async mounted () {
    this.readonly = this.$store.state.iconstatus
    this.$route.meta.title = this.$store.getters.subPage_name
    this.tab = this.$store.state.tab
  },
  methods: {
    tabchange () {
      this.$store.commit('setTab', this.tab)
    },
    handleEdit () {
      this.readonly = !this.readonly
      this.$store.commit('set_iconstatus', this.readonly)
    },
    handleDel () { },
  },
  computed: {
  },
  watch: {},
};
</script>

